<template>
  <div class="about-container">
    <h2>组件使用说明</h2>
    <!-- Avatar头像 -->
    <table border="2">
      <thead>
        <tr>
          <th colspan="5">Avatar头像</th>
        </tr>
        <tr>
          <th>属性名</th>
          <th>含义</th>
          <th>类型</th>
          <th>必填</th>
          <th>默认值</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in avatar" :key="item.arrtName">
          <td>{{ item.attrName }}</td>
          <td>{{ item.meaning }}</td>
          <td>{{ item.type }}</td>
          <td>{{ item.require }}</td>
          <td>{{ item.default }}</td>
        </tr>
      </tbody>
    </table>
    <!-- Icon图标 -->
    <table border="2">
      <thead>
        <tr>
          <th colspan="5">Icon图标</th>
        </tr>
        <tr>
          <th>属性名</th>
          <th>含义</th>
          <th>类型</th>
          <th>必填</th>
          <th>默认值</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>{{ icon.attrName }}</td>
          <td>{{ icon.meaning }}</td>
          <td>{{ icon.type }}</td>
          <td>{{ icon.require }}</td>
          <td>{{ icon.default }}</td>
        </tr>
      </tbody>
    </table>
    <!-- Pager分页 -->
    <table border="2">
      <thead>
        <tr>
          <th colspan="5">Pager分页</th>
        </tr>
        <tr>
          <th>属性名</th>
          <th>含义</th>
          <th>类型</th>
          <th>必填</th>
          <th>默认值</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in pager.attribute" :key="item.arrtName">
          <td>{{ item.attrName }}</td>
          <td>{{ item.meaning }}</td>
          <td>{{ item.type }}</td>
          <td>{{ item.require }}</td>
          <td>{{ item.default }}</td>
        </tr>
        <tr>
          <th>事件名</th>
          <th>含义</th>
          <th>事件参数</th>
          <th>参数类型</th>
          <th>——</th>
        </tr>
        <tr>
          <td>{{ pager.event.eventName }}</td>
          <td>{{ pager.event.meaning }}</td>
          <td>{{ pager.event.args }}</td>
          <td>{{ pager.event.argsType }}</td>
          <td>——</td>
        </tr>
      </tbody>
    </table>
    <!-- Empty 无数据 -->
    <table border="2">
      <thead>
        <tr>
          <th colspan="5">Empty无数据</th>
        </tr>
        <tr>
          <th>属性名</th>
          <th>含义</th>
          <th>类型</th>
          <th>必填</th>
          <th>默认值</th>
        </tr>
      </thead>
      <tbody>
        <tr>
          <td>text</td>
          <td>显示的文字</td>
          <td>String</td>
          <td>否</td>
          <td>"无数据"</td>
        </tr>
      </tbody>
    </table>
    <!-- ImageLoader图片模糊到显示 -->
    <table border="2">
      <thead>
        <tr>
          <th colspan="5">ImageLoader</th>
        </tr>
        <tr>
          <th>属性名</th>
          <th>含义</th>
          <th>类型</th>
          <th>必填</th>
          <th>默认值</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in imageLoader.attribute" :key="item.arrtName">
          <td>{{ item.attrName }}</td>
          <td>{{ item.meaning }}</td>
          <td>{{ item.type }}</td>
          <td>{{ item.require }}</td>
          <td>{{ item.default }}</td>
        </tr>
        <tr>
          <th>事件名</th>
          <th>含义</th>
          <th>事件参数</th>
          <th>参数类型</th>
          <th>——</th>
        </tr>
        <tr>
          <td>{{ imageLoader.event.eventName }}</td>
          <td>{{ imageLoader.event.meaning }}</td>
          <td>{{ imageLoader.event.args }}</td>
          <td>{{ imageLoader.event.argsType }}</td>
          <td>——</td>
        </tr>
      </tbody>
    </table>
    <!-- Commnet评论组件 -->
    <table border="2">
      <thead>
        <tr>
          <th colspan="5">Commnet评论组件</th>
        </tr>
        <tr>
          <th>属性名</th>
          <th>含义</th>
          <th>类型</th>
          <th>必填</th>
          <th>默认值</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in commnet.attribute" :key="item.arrtName">
          <td>{{ item.attrName }}</td>
          <td>{{ item.meaning }}</td>
          <td>{{ item.type }}</td>
          <td>{{ item.require }}</td>
          <td>{{ item.default }}</td>
        </tr>
        <tr>
          <th>事件名</th>
          <th>含义</th>
          <th>事件参数</th>
          <th>参数类型</th>
          <th>——</th>
        </tr>
        <tr>
          <td>{{ commnet.event.eventName }}</td>
          <td>{{ commnet.event.meaning }}</td>
          <td>{{ commnet.event.args }}</td>
          <td>{{ commnet.event.argsType }}</td>
          <td>——</td>
        </tr>
      </tbody>
    </table>
  </div>
</template>
<script>
export default {
  data() {
    return {
      avatar: [
        {
          attrName: "url",
          meaning: "头像图片路径",
          type: "String",
          require: "是",
          default: "无",
        },
        {
          attrName: "width",
          meaning: "头像尺寸，宽",
          type: "Number",
          require: " 否",
          default: "150",
        },
        {
          attrName: "height",
          meaning: "头像尺寸，高",
          type: "Number",
          require: " 否",
          default: "150",
        },
      ],
      icon: {
        attrName: "type",
        meaning: "图标类型",
        type: "String",
        require: "是",
        default: "无",
      },
      pager: {
        attribute: [
          {
            attrName: "current",
            meaning: "当前页码",
            type: "Number",
            require: "否",
            default: "1",
          },
          {
            attrName: "total",
            meaning: "总数据量",
            type: "Number",
            require: " 否",
            default: "0",
          },
          {
            attrName: "limit",
            meaning: "页容量",
            type: "Number",
            require: " 否",
            default: "10",
          },
          {
            attrName: "visibleNumber",
            meaning: "可见页码数",
            type: "Number",
            require: " 否",
            default: "10",
          },
        ],
        event: {
          eventName: "pageChange",
          meaning: "页码变化",
          args: "新的页码",
          argsType: "Number",
        },
      },
      imageLoader: {
        attribute: [
          {
            attrName: "src",
            meaning: "原始图片的路径",
            type: "String",
            require: "是",
            default: "无",
          },
          {
            attrName: "placeholder",
            meaning: "原始图片加载完成前的占位图片",
            type: "String",
            require: " 是",
            default: "无",
          },
          {
            attrName: "duration",
            meaning: "原始图片加载完成后触发",
            type: "Number",
            require: " 否",
            default: "500",
          },
        ],
        event: {
          eventName: "load",
          meaning: "原始图片加载完成后触发",
          args: "无",
          argsType: "无",
        },
      },
      commnet: {
        attribute: [
          {
            attrName: "title",
            meaning: "列表标题",
            type: "String",
            require: "否",
            default: "无",
          },
          {
            attrName: "commentCount",
            meaning: "评论的数量",
            type: "Number",
            require: "否",
            default: "无",
          },
          {
            attrName: "list",
            meaning: "列表数据",
            type: "Array",
            require: "否",
            default: "[]",
          },
          {
            attrName: "isLoading",
            meaning: "加载更多",
            type: "Boolean",
            require: "否",
            default: "false",
          },
        ],
        event: {
          eventName: "submit",
          meaning: "用户点击提交时触发",
          args: "数据对象",
          argsType: "Object",
        },
      },
    };
  },
};
</script>
<style lang="less" scoped>
@import "~@/styles/var.less";
.about-container {
  background: lighten(@gray, 25%);
  width: 100%;
  height: 100%;
  overflow-y: scroll;
  overflow-x: hidden;
  box-sizing: border-box;
  color: lighten(@dark, 10%);
  padding: 20px;
  h2 {
    text-align: center;
  }
  table {
    border-collapse: collapse;
    width: 100%;
    margin-bottom: 2em;
    caption {
      font-size: 2em;
    }
    td {
      text-align: center;
    }
  }
}
@media screen and (max-width: 600px) {
  .about-container{
     padding: 20px;
  }
}
</style>>